import React, { FC, useCallback, useState } from 'react';

import { withStyles } from '@material-ui/core/styles';
import { green } from '@material-ui/core/colors';
import PropTypes from 'prop-types';
import {
  Checkbox,
  Button,
  Container,
  Box,
  makeStyles,
  Card,
  CardHeader,
  CardContent,
  Hidden,
  TextField,
  Typography,
  CardMedia,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  Grid,
  Link,
  Tooltip,
  Avatar,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody
} from '@material-ui/core';
import { Link as RouterLink } from 'react-router-dom';
import Page from 'src/components/Page';
import Header from 'src/components/Header';
import { useAsync, useAsyncCallback } from 'react-async-hook';
import { useFormik } from 'formik';
import LoadingButton from 'src/components/LoadingButton';
import { NFTContractMap } from 'src/constants/nft';
import { getNFTInfo } from 'src/service';
import { NFTStatus, useNFTState } from 'src/hooks/nftState';
import InfoIcon from '@material-ui/icons/Info';
import { useTranslation } from 'react-i18next';
import { Opacity } from '@material-ui/icons';

interface NFTProps {}

const useStyles = makeStyles(theme => ({
  root: {},
  card: {
    maxWidth: 350,
    display: 'inline-block',
    margin: theme.spacing(1)
  },
  search: {
    '& > *': {
      marginRight: theme.spacing(1)
    }
  },
  img: {},
  formControl: {
    margin: theme.spacing(1),
    minWidth: 200
  },
  checkButton: {
    height: 56
  },
  table: {
    maxWidth: 650,
    marginBottom: theme.spacing(3)
  }
}));

const VerifiedIcon = () => <span>✅</span>;
const PendingIcon = () => <span>❗</span>;
const NotFoundIcon = () => <span>❓</span>;

const contracts = Object.keys(NFTContractMap);

const NFT: FC<NFTProps> = ({}) => {
  const classes = useStyles();
  // const history = useHistory();
  const { nftData, nftAsyncCheck } = useNFTState();
  const { t } = useTranslation();

  const formik = useFormik({
    initialValues: {
      contractId: NFTContractMap[contracts[0]],
      // contractId: null,
      tokenId: ''
    },
    onSubmit: () => {
      nftAsyncCheck.execute(values.contractId, values.tokenId);
    }
  });

  const {
    handleChange,
    handleSubmit,
    isSubmitting,
    touched,
    values,
    errors,
    setFieldValue
  } = formik;

  const { status, originData } = nftData;

  const Icon = {
    [NFTStatus.Verified]: VerifiedIcon,
    [NFTStatus.Pending]: PendingIcon,
    [NFTStatus.NotFound]: NotFoundIcon
  }[status];

  const title = {
    [NFTStatus.Verified]: t('tSage.verified'),
    [NFTStatus.Pending]: t('tSage.pending'),
    [NFTStatus.NotFound]: t('tSage.notFound')
  }[status];

  let rows = [];
  if (originData) {
    rows = [
      {
        author: originData.properties.artist,
        name: originData.name,
        desc: originData.description
      }
    ];
  }

  return (
    <Page className={classes.root} title={t('tSage.title')}>
      <Container maxWidth={false}>
        <Header title={t('tSage.title')} />
        <Grid container spacing={3} justify="center" alignItems="center">
          <form onSubmit={handleSubmit}>
            <Box className={classes.search} mb={3}>
              <FormControl
                required
                variant="outlined"
                className={classes.formControl}
              >
                {/* <InputLabel id="contractId-label">Contract ID</InputLabel> */}
                <Select
                  // label="NFT Contract ID"
                  // labelId="contractId-label"
                  name="contractId"
                  value={values.contractId}
                  onChange={handleChange}
                >
                  {contracts.map(contract => (
                    <MenuItem key={contract} value={NFTContractMap[contract]}>
                      {t("tSage."+contract)}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>

              <FormControl className={classes.formControl}>
                <TextField
                  size="medium"
                  label={t('tSage.nftId')}
                  variant="outlined"
                  required
                  value={values.tokenId}
                  name="tokenId"
                  onChange={handleChange}
                />
              </FormControl>
              <FormControl className={classes.formControl}>
                <LoadingButton
                  className={classes.checkButton}
                  variant="contained"
                  type="submit"
                  color="primary"
                  pending={nftAsyncCheck.loading}
                  // onClick={() => {
                  //   nftAsyncCheck.execute(values.contractId, values.tokenId);
                  // }}
                >
                  {t('tSage.check')}
                </LoadingButton>
              </FormControl>
            </Box>
          </form>
        </Grid>
        <Grid container spacing={3} justify="center" alignItems="center">
          {[NFTStatus.Verified, NFTStatus.Pending].includes(status) && (
            <Card className={classes.card}>
              <CardHeader avatar={<Icon />} title={title} />
              <CardMedia
                component="img"
                className={classes.img}
                src={nftData.oriMediaURI}
                title="nft"
              />
              {status === NFTStatus.Verified && (
                <CardContent>
                  <Grid container spacing={1}>
                    <Grid item xs={12} sm={6}>
                      <Tooltip
                        title={`SHA1(MerkleRoot): ${nftData.approvedHashSHA1}`}
                      >
                        <Avatar
                          alt="Etherscan"
                          component={RouterLink}
                          src={'tokens/ETH.png'}
                          to={
                            nftData.ethBlockId
                              ? `https://etherscan.io/block/${nftData.ethBlockId}`
                              : '#'
                          }
                        ></Avatar>
                      </Tooltip>
                      <Link
                        variant="body2"
                        href={
                          nftData.ethBlockId
                            ? `https://etherscan.io/block/${nftData.ethBlockId}`
                            : '#'
                        }
                        target="_blank"
                        style={{
                          marginRight: 12
                          // opacity:nftData.ethBlockId?100:0
                        }}
                      >
                        Etherscan
                      </Link>
                      {/* <InfoIcon /> */}
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <Tooltip title={`MerkleRoot: ${nftData.approvedHash}`}>
                        <Avatar
                          alt="Bitcoin Explore"
                          component={RouterLink}
                          src={'tokens/BTCB.png'}
                          to={
                            nftData.btcBlockId
                              ? `https://www.blockchain.com/btc/block/${nftData.btcBlockId}`
                              : '#'
                          }
                        ></Avatar>
                      </Tooltip>
                      <Link
                        variant="body2"
                        href={
                          nftData.btcBlockId
                            ? `https://www.blockchain.com/btc/block/${nftData.btcBlockId}`
                            : '#'
                        }
                        target="_blank"
                        style={{
                          marginRight: 12
                          // opacity:nftData.btcBlockId?100:0
                        }}
                      >
                        Bitcoin Explore
                      </Link>
                      {/* <InfoIcon /> */}
                    </Grid>
                  </Grid>
                </CardContent>
              )}
            </Card>
          )}
        </Grid>
        {status === NFTStatus.Verified && (
          <Grid container spacing={3} justify="center" alignItems="center">
            <Table className={classes.table}>
              <TableHead>
                <TableRow>
                  <TableCell>{t("tSage.name")}</TableCell>
                  <TableCell>{t("tSage.author")}</TableCell>
                  <TableCell>{t("tSage.desc")}</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {rows.map(row => (
                  <TableRow key={row.name}>
                    <TableCell>{row.name}</TableCell>
                    <TableCell>{row.author}</TableCell>
                    <TableCell>{row.desc}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </Grid>
        )}
      </Container>
    </Page>
  );
};

NFT.propTypes = {};

export default NFT;
