import { useAsync, useAsyncCallback } from 'react-async-hook';
import { toastConfig } from 'src/config';
import { useCallback, useEffect, useState, useMemo } from 'react';
import { getNFTContract } from 'src/utils/contract';
import { useActiveWeb3React } from './useActiveWeb3React';
import { getNFTInfo } from 'src/service';
import axios from 'axios';
import { toast } from 'react-toastify';
import { BigNumber, utils } from 'ethers';

const combinedHash = (first: Buffer, second: Buffer): Buffer => {
  if (!first) {
    return second;
  }
  if (!second) {
    return first;
  }

  return Buffer.from(
    utils
      .solidityKeccak256(
        ['bytes32', 'bytes32'],
        [first, second].sort(Buffer.compare)
      )
      .slice(2),
    'hex'
  );
};

const toNode = (
  index: number | BigNumber,
  account: string,
  amount: BigNumber
): Buffer => {
  const pairHex = utils.solidityKeccak256(
    ['uint256', 'string', 'uint256'],
    [index, account, amount]
  );
  return Buffer.from(pairHex.slice(2), 'hex');
};

const verifyProof = (
  index: number | BigNumber,
  account: string,
  amount: BigNumber,
  proof: Buffer[],
  root: Buffer
): boolean => {
  let pair = toNode(index, account, amount);
  for (const item of proof) {
    pair = combinedHash(pair, item);
  }

  return pair.equals(root);
};

export enum NFTStatus {
  Verified,
  Pending,
  NotFound,
  Failed
}

export const useNFTState = () => {
  const { account, library } = useActiveWeb3React();
  const [nftData, setNftData] = useState<any>({});

  const nftAsyncCheck = useAsyncCallback(
    async (contractId: string, tokenId: string) => {
      try {
        const data = await getNFTInfo('bsc', contractId, tokenId);
        if (data.code === 0 && data.data) {
          const info = data.data;

          if (!info.approvedHash) {
            setNftData({
              status: NFTStatus.Pending,
              ...info
            });
          } else {
            const nftContract = getNFTContract(contractId, library, account);
            const tokenURI: string = await nftContract.tokenURI(tokenId);
            const proofResult = verifyProof(
              info.proofIndex,
              `${info.blockchain}-${info.contract}-${info.nftId}`,
              info.nftId,
              info.proof
                .split(',')
                .map((p: string) => Buffer.from(p.slice(2), 'hex')),
              Buffer.from(info.approvedHash.slice(2), 'hex')
            );

            if (!proofResult) {
              setNftData({
                status: NFTStatus.Failed,
                ...info
              });

              toast.error(`Verify Failed`, toastConfig);
              return;
            }

            const { data: originData }: any = await axios.get(tokenURI);
            // if (originData && originData.image === info.oriMediaURI) {
              setNftData({
                status: NFTStatus.Verified,
                ...info,
                originData
              });
            // }
          }
        } else {
          setNftData({
            status: NFTStatus.NotFound
          });
          toast.error(`Not Found`, toastConfig);
        }
      } catch (e) {
        console.log(e);
        setNftData({
          status: NFTStatus.NotFound
        });
        toast.error(`Not Found`, toastConfig);
      }
    }
  );

  return {
    nftData,
    nftAsyncCheck
  };
};
